<template>
  <v-container justify="center">
    <v-flex>
      <v-card class="elevation-2">
        <v-card-title class="headline justify-center blue--text">
          {{ itemactual.referencia + ' - ' + itemactual.titulo }}
        </v-card-title>
        <v-card-text>
          <v-text-field
            v-model="search"
            append-icon="search"
            label="Buscar"
            single-line
            hide-details
            >
          </v-text-field>
          <br>
          <v-data-table
           :headers="headers"
           :items="dataNovedades"
           :search="search"
           class="elevation-1"
          >
            <template v-slot:item.archivo="{ item }">
              <v-icon
                class="mr-5"
                color="primary"
                @click="downloadFile(item.rutasarchivo, item.archivo)"
              >
                file_download
              </v-icon>
            </template>
          </v-data-table>
        </v-card-text>
      </v-card>
     </v-flex>
   </v-container>
</template>
<script>
import { anunciosProcAdquisicion } from '@/services/api'
import axios from 'axios'
export default {
  name: 'anunciosTabNov',
  props: ['itemactual'],
  data () {
    return {
      search: '',
      dataNovedades: [],
      headers: [
        { text: 'DescripciÃ³n', align: 'left', value: 'descripcion', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Fecha', align: 'center', value: 'fecha', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Descargar', align: 'center', value: 'archivo', class: 'subtitle-1 font-weight-bold black--text' }
      ]
    }
  },
  methods: {
    async getNovedades () {
      const { data } = await anunciosProcAdquisicion.getNovedades({
        codigoproceso: this.itemactual.id
      })
      this.dataNovedades = data.data
    },
    downloadFile (url, nombreArchivo) {
      axios({
        method: 'get',
        url: url,
        responseType: 'arraybuffer'
      })
        .then(response => {
          this.forceFileDownload(response, nombreArchivo)
        })
        .catch(() => console.log('error occured'))
    },
    forceFileDownload (response, nombreArchivo) {
      const url = window.URL.createObjectURL(new Blob([response.data]))
      const link = document.createElement('a')
      link.href = url
      link.setAttribute('download', nombreArchivo) // or any other extension
      document.body.appendChild(link)
      link.click()
    }
  },
  watch: {
    itemactual: {
      handler: function (val) {
        if (val) {
          this.getNovedades()
        }
      },
      immediate: true // provides initial (not changed yet) state
    }
  },
  mounted () {
    this.getNovedades()
  }
}
</script>
